.pokedox {
  overflow: auto;
  height: 100vh;
  width: 100vw;
  padding: 0.2rem;
  display: flex;
  background-image: url(../assets/images/pokemon-background.jpg);
  background-repeat: repeat;
  background-size: contain;
}

.pokedox__containers {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
}

.pokedox__leftContainer {
  border-radius: 2rem;
  width: 30rem;
  height: 35rem;
  background-color: #ff0000;
  border: 5px solid black;
}

.sidebar__search {
  display: flex;
  align-items: center;
  background-color: #f6f6f6;
  height: 2rem;
  padding: 0.7rem;
  border-radius: 0.8rem;
  margin: 1rem;
}

.sidebar__searchContainer {
  display: flex;
  align-items: center;
  background-color: white;
  width: 100%;
  height: 2rem;
  border-radius: 0.6rem;
}

.sidebar__searchContainer > input {
  border: none;
  margin-left: 0.6rem;
  outline: none;
  flex: 1;
}

/* MuiSvgIcon-root is class name of the material-ui icon */
.sidebar__searchContainer > .MuiSvgIcon-root {
  color: gray;
}

.pokemon__image {
  border-radius: 1rem;
  border: 1rem solid rgb(144, 143, 143);
  width: 26rem;
  height: 26rem;
  margin: 1rem;
  background-color: rgb(234, 223, 223);
}

.pokemon__image > img {
  display: flex;
  width: 100%;
  background-color: rgb(234, 223, 223);
}
.pokedox__rightContainer {
  border-radius: 2rem;
  border: 5px solid black;
  width: 30rem;
  height: 35rem;
  background-color: #ff0000;
  font-family: "Press Start 2P", cursive;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  flex-direction: column;
}

.pokemon__bio {
  border: 2px solid black;
  border-radius: 1rem;
  background-color: rgb(26, 238, 47);
  width: 23rem;
  height: 22rem;
  margin: 2rem 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 1rem;
}
.pokemon__bio > h1 {
  text-transform: capitalize;
  font-size: 2rem;
}

.pokemon__bio > h2 {
  font-size: 1.5rem;
}
.pokemon__bio > h3 {
  font-size: 1.2rem;
}

.pokedox__headerButtons {
  align-self: flex-start;
}

.pokedox__headerButtons > .circle {
  margin: 0.5rem;
}

#blue-circle {
  height: 5rem;
  width: 5rem;
  color: rgb(69, 200, 252);
  background-color: rgb(234, 223, 223);
  border-radius: 3rem;
}

#red-circle {
  color: red;
  background-color: red;
  border: 2px solid black;
  border-radius: 1rem;
}
#yellow-circle {
  color: yellow;
  background-color: yellow;
  border: 2px solid black;
  border-radius: 1rem;
}
#green-circle {
  color: rgb(26, 238, 47);
  background-color: rgb(26, 238, 47);
  border: 2px solid black;
  border-radius: 1rem;
}

@media only screen and (max-width: 999px) {
  .pokedox__containers {
    flex-direction: column;
  }
  .title {
    font-size: 0.6rem;
    right: 3%;
    padding: 0.2rem;
  }
  .pokemon__names {
    display: none;
  }
}

@media only screen and (max-width: 517px) {
  * {
    font-size: 10px;
  }
  .title {
    font-size: 0.3rem;
    right: 1.5%;
    padding: 0.1rem;
  }
}
@media only screen and (max-width: 386px) {
  * {
    font-size: 8px;
  }
  .title {
    display: none;
  }
}
